         :current-page.sync="currentPage1"
         :page-size="5"
         layout="total, prev, pager, next"
         :total="this.allData.length">
        </el-pagination>

      
      <el-table
        :data="allData.slice((currentPage1-1)*5,currentPage1*5)"
        stripe
        :border="true"
        height="550"
        style="text-align:left;">
        <el-table-column
          prop="orderid"
          label="订单号"
          width="80">
           <template slot-scope="scope">
                       <h5>{{scope.row.orderid}}</h5>
            </template>
        </el-table-column>
        <el-table-column
          prop="date"
          label="下单日期"
          width="80">
           <template slot-scope="scope">
                       <h5>{{scope.row.date}}</h5>
            </template>
        </el-table-column>
        <el-table-column
          prop="goodsname"
          label="所订购产品"
          width="50">
           <template slot-scope="scope">
                     <h5>{{scope.row.goodsname}}</h5>
            </template>
        </el-table-column>
        <el-table-column
          prop="pay"
          label="成交金额"
          width="50">
           <template slot-scope="scope">
                    <h5>{{scope.row.pay}}</h5>
            </template>
        </el-table-column>
        <el-table-column
          prop="address"
          label="收货地址"
          width="230">
           <template slot-scope="scope">
                      <h5>{{scope.row.address}}</h5>
            </template>
        </el-table-column>
        <el-table-column
          prop="name"
          label="收件人姓名"
          width="100">
           <template slot-scope="scope">
                      <h5>{{scope.row.name}}</h5>
            </template>
        </el-table-column>
        <el-table-column
          prop="phone"
          label="收货人手机号"
          width="115">
           <template slot-scope="scope">  
                      <h5>{{scope.row.phone}}</h5>
            </template>
        </el-table-column>
        <el-table-column
          prop="sentflag"
          label="是否发货"
          width="50">
           <template slot-scope="scope">
