<script>
import axios from 'axios';
import { mapGetters, mapActions} from 'vuex'
  export default {
    data() {
      return {
        currentPage1: 1,
        allData: [{orderid:"D1524987",date:"2018-05-12 12:34",goodsname:"荔枝",pay:"150",address:"广西钦州永福大街15号",name:"张三",phone:"15607812256",sentflag:"",sendedcode:""},
                 {orderid:"D1524987",date:"2018-05-12 12:34",goodsname:"荔枝",pay:"150",address:"广西钦州永福大街15号",name:"张三",phone:"15607812256",sentflag:"",sendedcode:""},
                 {orderid:"D1524987",date:"2018-05-12 12:34",goodsname:"荔枝",pay:"150",address:"广西钦州永福大街15号",name:"张三",phone:"15607812256",sentflag:"",sendedcode:""},
                 {orderid:"D1524987",date:"2018-05-12 12:34",goodsname:"荔枝",pay:"150",address:"广西钦州永福大街15号",name:"张三",phone:"15607812256",sentflag:"",sendedcode:""},
                 {orderid:"D1524987",date:"2018-05-12 12:34",goodsname:"荔枝",pay:"150",address:"广西钦州永福大街15号",name:"张三",phone:"15607812256",sentflag:"",sendedcode:""},
                 {orderid:"D1524987",date:"2018-05-12 12:34",goodsname:"荔枝",pay:"150",address:"广西钦州永福大街15号",name:"张三",phone:"15607812256",sentflag:"",sendedcode:""},
                 {orderid:"D1524987",date:"2018-05-12 12:34",goodsname:"荔枝",pay:"150",address:"广西钦州永福大街15号",name:"张三",phone:"15607812256",sentflag:"",sendedcode:""},
                 {orderid:"D1524987",date:"2018-05-12 12:34",goodsname:"荔枝",pay:"150",address:"广西钦州永福大街15号",name:"张三",phone:"15607812256",sentflag:"",sendedcode:""},
                 {orderid:"D1524987",date:"2018-05-12 12:34",goodsname:"荔枝",pay:"150",address:"广西钦州永福大街15号",name:"张三",phone:"15607812256",sentflag:"",sendedcode:""},  
        ]
      }
    },
    computed:{
      ...mapGetters([
      'getlocalapi',
    ])},
    methods:{
      handleCurrentChange(val) {
        // console.log(`当前页: ${val}`);
      },
      handremake(singlegoods){
      //  this.$store.dispatch('resetProducts',singlegoods)
      },
      handdelectde(x) {
         console.log(x)
        var postdata={"mutationType":"delectonegoods","goodsid":x}
        
          axios.post(this.getlocalapi+"/goods",JSON.stringify(postdata)) 
          .then(response => {
            if(response.data.code=="ok"){
             this.getdata()
              const h = this.$createElement;
              this.$message({
                message: h('p', null, [
                  h('span', null, '删除成功 '),
                  h('i', { style: 'color: teal' }, '')
                ])
              });
              
            }
          })
          .catch(err => {
           console.log(err);
          }) 
       
      },
      handsentdata(data){
         console.log(data.sentflag)
         console.log(data.sendedcode)
      },
      senddata(){
       Date.prototype.pattern=function(fmt) {
         var o = {
         "M+" : this.getMonth()+1, //月份
         "d+" : this.getDate(), //日
         "h+" : this.getHours()%12 == 0 ? 12 : this.getHours()%12, //小时
         "H+" : this.getHours(), //小时
          "m+" : this.getMinutes(), //分
          "s+" : this.getSeconds(), //秒
           "q+" : Math.floor((this.getMonth()+3)/3), //季度
          "S" : this.getMilliseconds() //毫秒
        };
         var week = {
           "0" : "/u65e5",
           "1" : "/u4e00",
           "2" : "/u4e8c",
           "3" : "/u4e09",
           "4" : "/u56db",
           "5" : "/u4e94",
           "6" : "/u516d"
          };
          if(/(y+)/.test(fmt)){
              fmt=fmt.replace(RegExp.$1, (this.getFullYear()+"").substr(4 - RegExp.$1.length));
          }
            if(/(E+)/.test(fmt)){
                fmt=fmt.replace(RegExp.$1, ((RegExp.$1.length>1) ? (RegExp.$1.length>2 ? "/u661f/u671f" : "/u5468") : "")+week[this.getDay()+""]);
            }
           for(var k in o){
             if(new RegExp("("+ k +")").test(fmt)){
             fmt = fmt.replace(RegExp.$1, (RegExp.$1.length==1) ? (o[k]) : (("00"+ o[k]).substr((""+ o[k]).length)));
            }
           }
          return fmt;
        }
        var postdata={"mutationType":"getallgoods"}
        axios.post(this.getlocalapi+"/goods",JSON.stringify(postdata)) 
        .then(response => {
             var iarr=[]
             for(var i=0;i<response.data.length;i++){
                var t=response.data[i].googsloadtime
                t= new Date(t).pattern("yyyy-MM-dd hh:mm:ss")
                response.data[i].googsloadtime=t
                iarr.push(response.data[i])

             }
             this.allData=iarr
        })
       .catch(err => {
           console.log(err);
        })   
      }
    },
    created() {
    },
  }
</script>
<style scoped>
#mytable{
  height:5rem;
  width:35rem;
  margin-top:1.5rem;
}
el-table{
  text-align:center;
}
#goodsint{
  width:10rem;
  color:red;
  overflow:hidden;
  margin:auto;
}
</style>


